* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: "AntennaExtraCond";
  line-height: 1;
  color: var(--black);
  background-color: transparent;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  font-family: inherit;
}

input,
textarea,
select {
  font-family: inherit;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  font-family: "Antenna", Arial, sans-serif;
  font-weight: 400;
  line-height: 1.4;
  background: transparent;
  color: var(--text);
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-size: 3rem;
  font-weight: 700;
  line-height: 1.4;
  margin: 0;
}

h2 {
  font-size: 2.625rem;
  font-weight: 500;
  line-height: 1.4;
  margin: 0;
}

h3 {
  font-size: 2.25rem;
  font-weight: 600;
  line-height: 1.4;
  margin: 0;
}

h4 {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.4;
  margin: 0;
}

h5 {
  font-size: 1.375rem;
  font-weight: 500;
  line-height: 1.4;
  margin: 0;
}

h6 {
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.4;
  margin: 0;
}

p {
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.4;
  margin: 0;
}

main.main {
  position: relative;
  width: 100%;
  min-height: 100vh;
}
@media (max-width: 991px) {
  main.main {
    min-height: auto;
  }
}

.container-xl {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (max-width: 991px) {
  .container-xl {
    padding: 0 16px;
  }
}

.topbar {
  background: #FFFFFF;
  padding: 18px 0;
  position: relative;
  z-index: 1020;
}
@media (max-width: 991px) {
  .topbar {
    padding: 12px 0;
  }
}
.topbar__content {
  gap: 99px;
}
@media (max-width: 991px) {
  .topbar__content {
    flex-wrap: wrap;
    gap: 24px;
  }
}
.topbar__logo {
  flex-shrink: 0;
}
.topbar__logo img {
  display: block;
  height: 72px;
  width: auto;
}
@media (max-width: 991px) {
  .topbar__logo img {
    height: 56px;
  }
}
@media (max-width: 767px) {
  .topbar__logo img {
    height: 48px;
  }
}
.topbar__center {
  max-width: 486px;
  flex: 1;
}
@media (max-width: 991px) {
  .topbar__center {
    order: 3;
    width: 100%;
    max-width: 100%;
    text-align: center;
  }
}
.topbar__title {
  font-family: "Antenna", Arial, sans-serif;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 1;
  color: #212529;
  margin: 0;
}
@media (max-width: 767px) {
  .topbar__title {
    font-size: 0.625rem;
  }
}
.topbar__right {
  gap: 36px;
  flex-shrink: 0;
}
@media (max-width: 991px) {
  .topbar__right {
    gap: 24px;
    width: 100%;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .topbar__right {
    flex-wrap: wrap;
    gap: 16px;
  }
}
.topbar__info {
  gap: 16px;
}
@media (max-width: 767px) {
  .topbar__info {
    width: 100%;
    flex-wrap: wrap;
    gap: 12px;
  }
}
.topbar__info-item {
  gap: 6px;
  border-radius: 8px;
  white-space: nowrap;
}
.topbar__info-item i {
  font-size: 15px;
  color: #212529;
}
@media (max-width: 767px) {
  .topbar__info-item i {
    font-size: 13px;
  }
}
.topbar__info-item span {
  font-family: "Antenna", Arial, sans-serif;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 1;
  color: #212529;
}
@media (max-width: 767px) {
  .topbar__info-item span {
    font-size: 0.625rem;
  }
}
.topbar__divider {
  width: 0;
  height: 20px;
  border-left: 1px solid rgba(33, 37, 41, 0.2);
  transform: rotate(90deg);
  flex-shrink: 0;
}
@media (max-width: 767px) {
  .topbar__divider {
    display: none;
  }
}
.topbar__actions {
  gap: 36px;
}
@media (max-width: 991px) {
  .topbar__actions {
    gap: 24px;
  }
}
@media (max-width: 767px) {
  .topbar__actions {
    width: 100%;
    justify-content: space-around;
  }
}
.topbar__language {
  gap: 6px;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s ease;
  padding: 4px 8px;
}
.topbar__language:hover {
  background: rgba(33, 37, 41, 0.05);
}
.topbar__language span {
  font-family: "Antenna", Arial, sans-serif;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 1;
  color: #212529;
}
